@media (max-width: 992px) {
  #top .nav {
    position: fixed;
    bottom: 0;
    z-index: 4;
    width: 100vw;
    right: -105vw;
    height: calc(100vh - 70px);
    background: var(--netflix-black);
    box-shadow: 0 0 4px 4px rgba(var(--netflix-black-rgb), 0.5);
    border-radius: 4px;
    align-items: center;
  }
  #top .nav .nav-link {
    transition: filter 0.25s ease;
